import React from 'react';
import { useUser } from '../contexts/UserContext';
import { Navigate } from 'react-router-dom';
import UserManagement from './UserManagement';
import { 
  UserGroupIcon,
  ChartBarIcon,
  CogIcon,
  ServerStackIcon,
  ShieldCheckIcon
} from '@heroicons/react/24/outline';

const AdminPanel = () => {
  const { isAdmin, getAllUsers } = useUser();

  if (!isAdmin()) {
    return <Navigate to="/" replace />;
  }

  const users = getAllUsers();
  const userStats = {
    total: users.length,
    admins: users.filter(u => u.role === 'admin').length,
    managers: users.filter(u => u.role === 'manager').length,
    users: users.filter(u => u.role === 'user').length
  };

  const StatCard = ({ title, value, icon: Icon, color = 'blue' }) => {
    const colorClasses = {
      blue: 'bg-blue-500',
      green: 'bg-green-500',
      yellow: 'bg-yellow-500',
      red: 'bg-red-500'
    };

    return (
      <div className="bg-white rounded-lg shadow p-6">
        <div className="flex items-center">
          <div className={`${colorClasses[color]} rounded-md p-3`}>
            <Icon className="h-6 w-6 text-white" />
          </div>
          <div className="ml-4">
            <p className="text-sm font-medium text-gray-600">{title}</p>
            <p className="text-2xl font-semibold text-gray-900">{value}</p>
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="max-w-7xl mx-auto p-6">
      {/*  */}
      <div className="mb-8">
        <div className="flex items-center justify-between mb-2">
          <div className="flex items-center space-x-3">
            <ShieldCheckIcon className="h-8 w-8 text-blue-600" />
            <div>
              <h1 className="text-3xl font-bold text-gray-900"> </h1>
            </div>
          </div>
          
          <div className="flex items-center space-x-4" />
        </div>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <StatCard
          title=" "
          value={userStats.total}
          icon={UserGroupIcon}
          color="blue"
        />
        <StatCard
          title=""
          value={userStats.admins}
          icon={ShieldCheckIcon}
          color="red"
        />
        <StatCard
          title=" "
          value={userStats.users}
          icon={UserGroupIcon}
          color="green"
        />
      </div>

      {/*   */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
        {/*   */}
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center space-x-3 mb-4">
            <ServerStackIcon className="h-6 w-6 text-gray-600" />
            <h3 className="text-lg font-semibold text-gray-900"> </h3>
          </div>
          <div className="space-y-3">
            <div className="flex justify-between">
              <span className="text-gray-600"></span>
              <span className="font-medium">v1.0.0</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600"> </span>
              <span className="font-medium">LocalStorage</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600"> </span>
              <span className="font-medium"></span>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center space-x-3 mb-4">
            <ChartBarIcon className="h-6 w-6 text-gray-600" />
            <h3 className="text-lg font-semibold text-gray-900"> </h3>
          </div>
          <div className="space-y-3">
            <div className="text-sm">
              <p className="text-gray-600"> </p>
              <p className="font-medium">{userStats.total}</p>
            </div>
            <div className="text-sm">
              <p className="text-gray-600"> </p>
              <p className="font-medium">1</p>
            </div>
            <div className="text-sm">
              <p className="text-gray-600"> </p>
              <p className="font-medium text-green-600"></p>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center space-x-3 mb-4">
            <CogIcon className="h-6 w-6 text-gray-600" />
            <h3 className="text-lg font-semibold text-gray-900"> </h3>
          </div>
          <div className="space-y-3">
            <button className="w-full text-left p-2 rounded-md hover:bg-gray-50 text-sm">
                 
            </button>
            <button className="w-full text-left p-2 rounded-md hover:bg-gray-50 text-sm">
                
            </button>
            <button className="w-full text-left p-2 rounded-md hover:bg-gray-50 text-sm">
                
            </button>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h2 className="text-xl font-semibold text-gray-900"></h2>
        </div>
        <div className="p-0">
          <UserManagement />
        </div>
      </div>

      {/*   */}
      <div className="mt-8 bg-yellow-50 border border-yellow-200 rounded-md p-4">
        <div className="flex">
          <div className="flex-shrink-0">
            <ShieldCheckIcon className="h-5 w-5 text-yellow-400" />
          </div>
          <div className="ml-3">
            <h3 className="text-sm font-medium text-yellow-800">
               
            </h3>
            <div className="mt-2 text-sm text-yellow-700">
              <ul className="list-disc pl-5 space-y-1">
                <li>   </li>
                <li>   </li>
                <li>   </li>
                <li>   </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      {/*    UI  . */}
    </div>
  );
};

export default AdminPanel;
