import React, { createContext, useContext, useState, useEffect } from 'react';

const UserContext = createContext();

export const useUser = () => {
  const context = useContext(UserContext);
  if (!context) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
};

export const UserProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const defaultUsers = [
      { id: 1, username: 'admin', password: 'admin123', name: '', role: 'admin' },
      { id: 2, username: 'manager', password: 'manager123', name: '', role: 'manager' },
      { id: 3, username: 'user', password: 'user123', name: '', role: 'user' }
    ];
    const users = localStorage.getItem('CMS_USERS');
    if (!users) {
      localStorage.setItem('CMS_USERS', JSON.stringify(defaultUsers));
    }

    const savedUser = sessionStorage.getItem('CURRENT_USER');
    if (savedUser) {
      setCurrentUser(JSON.parse(savedUser));
      setIsLoggedIn(true);
    } else {
      //    
      localStorage.removeItem('CURRENT_USER');
    }
  }, []);

  const login = (username, password) => {
    const users = JSON.parse(localStorage.getItem('CMS_USERS') || '[]');
    const user = users.find(u => u.username === username && u.password === password);
    
    if (user) {
      setCurrentUser(user);
      setIsLoggedIn(true);
      sessionStorage.setItem('CURRENT_USER', JSON.stringify(user));
      return { success: true, user };
    } else {
      return { success: false, error: '    .' };
    }
  };

  const logout = () => {
    setCurrentUser(null);
    setIsLoggedIn(false);
    try { sessionStorage.removeItem('CURRENT_USER'); } catch (e) {}
    try { localStorage.removeItem('CURRENT_USER'); } catch (e) {}
    //    
    try {
      localStorage.setItem('CMS_LOGOUT', String(Date.now()));
      localStorage.removeItem('CMS_LOGOUT');
    } catch (e) {}
  };

  const getUserStorageKey = (username, key) => {
    return `USER_${username}_${key}`;
  };

  const getAllUsers = () => {
    return JSON.parse(localStorage.getItem('CMS_USERS') || '[]');
  };

  const addUser = (userData) => {
    const users = getAllUsers();
    const newUser = {
      ...userData,
      id: Math.max(0, ...users.map(u => u.id)) + 1
    };
    
    if (users.find(u => u.username === userData.username)) {
      return { success: false, error: '  .' };
    }

    users.push(newUser);
    localStorage.setItem('CMS_USERS', JSON.stringify(users));
    return { success: true, user: newUser };
  };

  const updateUser = (userId, userData) => {
    const users = getAllUsers();
    const userIndex = users.findIndex(u => u.id === userId);
    
    if (userIndex === -1) {
      return { success: false, error: '   .' };
    }

    const existingUser = users.find(u => u.username === userData.username && u.id !== userId);
    if (existingUser) {
      return { success: false, error: '  .' };
    }

    users[userIndex] = { ...users[userIndex], ...userData };
    localStorage.setItem('CMS_USERS', JSON.stringify(users));
    
    if (currentUser && currentUser.id === userId) {
      const updatedUser = users[userIndex];
      setCurrentUser(updatedUser);
      localStorage.setItem('CURRENT_USER', JSON.stringify(updatedUser));
    }

    return { success: true, user: users[userIndex] };
  };

  const deleteUser = (userId) => {
    const users = getAllUsers();
    const filteredUsers = users.filter(u => u.id !== userId);
    
    if (users.length === filteredUsers.length) {
      return { success: false, error: '   .' };
    }

    localStorage.setItem('CMS_USERS', JSON.stringify(filteredUsers));
    
    if (currentUser && currentUser.id === userId) {
      logout();
    }

    return { success: true };
  };

  const isAdmin = () => {
    return currentUser && currentUser.role === 'admin';
  };

  const value = {
    currentUser,
    isLoggedIn,
    login,
    logout,
    getUserStorageKey,
    getAllUsers,
    addUser,
    updateUser,
    deleteUser,
    isAdmin
  };

  // 1   
  useEffect(() => {
    if (!isLoggedIn) return;
    let timerId;
    const TIMEOUT = 60 * 60 * 1000;
    const reset = () => {
      clearTimeout(timerId);
      timerId = setTimeout(() => {
        logout();
      }, TIMEOUT);
    };
    const events = ['mousemove', 'mousedown', 'keydown', 'scroll', 'touchstart'];
    events.forEach(ev => window.addEventListener(ev, reset, { passive: true }));
    reset();
    return () => {
      clearTimeout(timerId);
      events.forEach(ev => window.removeEventListener(ev, reset));
    };
  }, [isLoggedIn]);

  //    , /   Storage  .
  //  beforeunload/pagehide     .

  //       
  useEffect(() => {
    const onStorage = (e) => {
      if (e.key === 'CMS_LOGOUT') {
        setCurrentUser(null);
        setIsLoggedIn(false);
        try { sessionStorage.removeItem('CURRENT_USER'); } catch (err) {}
        try { localStorage.removeItem('CURRENT_USER'); } catch (err) {}
      }
    };
    window.addEventListener('storage', onStorage);
    return () => window.removeEventListener('storage', onStorage);
  }, []);

  return (
    <UserContext.Provider value={value}>
      {children}
    </UserContext.Provider>
  );
};

//  :  /     
// ( CURRENT_USER        )
//     

export default UserProvider;
