import React, { useState, useRef, useMemo } from 'react';
import { useApp } from '../contexts/AppContext';
import { exportToExcel, importFromExcel, createTemplate } from '../utils/excelUtils';

//     
function formatPhoneKR(input) {
  const digits = String(input || '').replace(/\D/g, '');
  if (digits.startsWith('02')) {
    if (digits.length <= 2) return digits;
    if (digits.length <= 5) return `${digits.slice(0, 2)}-${digits.slice(2)}`;
    if (digits.length <= 9) return `${digits.slice(0, 2)}-${digits.slice(2, digits.length - 4)}-${digits.slice(digits.length - 4)}`;
    return `${digits.slice(0, 2)}-${digits.slice(2, 6)}-${digits.slice(6, 10)}`; // 02-xxxx-xxxx
  }
  // (010 )   (3)
  if (digits.length <= 3) return digits;
  if (digits.length <= 7) return `${digits.slice(0, 3)}-${digits.slice(3)}`;
  if (digits.length <= 11) return `${digits.slice(0, 3)}-${digits.slice(3, digits.length - 4)}-${digits.slice(digits.length - 4)}`;
  return `${digits.slice(0, 3)}-${digits.slice(3, 7)}-${digits.slice(7, 11)}`; //  11
}

function Clients() {
  const { clients, setClients, invoices, workItems } = useApp();
  const [selectedIds, setSelectedIds] = useState([]);

  const [showModal, setShowModal] = useState(false);
  const [selectedClient, setSelectedClient] = useState(null);
  const [isEditing, setIsEditing] = useState(false);
  const [editingClientId, setEditingClientId] = useState(null);
  const [newClient, setNewClient] = useState({
    type: 'PERSON', // 'PERSON' | 'BUSINESS'
    name: '',
    phone: '',
    mobile: '',
    email: '',
    address: '',
    notes: '',
    business: {
      businessName: '', // 
      representative: '', // 
      businessNumber: '', // 10
      businessType: '', // 
      businessItem: '', // 
      businessAddress: '',
      taxEmail: ''
    },
    workplaces: [{ name: '', address: '', description: '' }]
  });

  const fileInputRef = useRef(null);
  const [showConfirmDelete, setShowConfirmDelete] = useState(false);

  //  /   
  const allVisibleIds = clients.map(c => c.id);
  const allSelected = selectedIds.length > 0 && selectedIds.length === allVisibleIds.length;
  const toggleSelectAll = (checked) => setSelectedIds(checked ? allVisibleIds : []);
  const toggleSelectOne = (id, checked) => setSelectedIds(prev => checked ? Array.from(new Set([...prev, id])) : prev.filter(x => x !== id));
  const handleBulkDelete = () => {
    if (selectedIds.length === 0) return;
    setClients(prev => prev.filter(c => !selectedIds.includes(c.id)));
    setSelectedIds([]);
    setShowConfirmDelete(false);
  };

  // Excel  
  const handleExportToExcel = () => {
    exportToExcel.clients(clients);
  };

  const handleImportFromExcel = async (e) => {
    const file = e.target.files[0];
    if (file) {
      try {
        const importedClients = await importFromExcel.clients(file);
        setClients(prev => [...prev, ...importedClients]);
        alert(`${importedClients.length}   .`);
      } catch (error) {
        alert('Excel     : ' + error.message);
      }
      //   
      e.target.value = '';
    }
  };

  const handleDownloadTemplate = () => {
    createTemplate.clients();
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    const next = (name === 'phone' || name === 'mobile') ? formatPhoneKR(value) : value;
    setNewClient(prev => ({
      ...prev,
      [name]: next
    }));
  };

  const formatBizNo = (val) => {
    const d = String(val || '').replace(/\D/g, '').slice(0, 10);
    if (d.length <= 3) return d;
    if (d.length <= 5) return `${d.slice(0,3)}-${d.slice(3)}`;
    return `${d.slice(0,3)}-${d.slice(3,5)}-${d.slice(5)}`;
  };

  const handleBusinessChange = (e) => {
    const { name, value } = e.target;
    const v = name === 'businessNumber' ? formatBizNo(value) : value;
    setNewClient(prev => ({
      ...prev,
      business: {
        ...prev.business,
        [name]: v
      }
    }));
  };

  const handleWorkplaceChange = (index, field, value) => {
    const updatedWorkplaces = [...newClient.workplaces];
    updatedWorkplaces[index][field] = value;
    setNewClient(prev => ({
      ...prev,
      workplaces: updatedWorkplaces
    }));
  };

  const addWorkplace = () => {
    setNewClient(prev => ({
      ...prev,
      workplaces: [...prev.workplaces, { name: '', address: '', description: '' }]
    }));
  };

  const removeWorkplace = (index) => {
    if (newClient.workplaces.length > 1) {
      const updatedWorkplaces = newClient.workplaces.filter((_, i) => i !== index);
      setNewClient(prev => ({
        ...prev,
        workplaces: updatedWorkplaces
      }));
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const payload = { ...newClient };
    if (payload.type === 'BUSINESS') {
      //    :   
      if (!payload.name && payload.business?.businessName) {
        payload.name = payload.business.businessName;
      }
      //    (): 10 
      const digits = String(payload.business?.businessNumber || '').replace(/\D/g, '');
      if (digits.length > 0 && digits.length !== 10) {
        alert('  10 .');
        return;
      }
    }
    
    if (isEditing) {
      //  
      const updatedClient = {
        ...payload,
        id: editingClientId,
        workplaces: newClient.workplaces.map((wp, index) => ({
          ...wp,
          id: wp.id || index + 1
        })),
        projects: Array.from(new Set((newClient.workplaces || [])
          .map(wp => (wp.description || '').trim())
          .filter(Boolean)))
      };
      setClients(prev => prev.map(client => 
        client.id === editingClientId 
          ? { ...client, ...updatedClient, totalBilled: client.totalBilled, outstanding: client.outstanding }
          : client
      ));
    } else {
      //   
      const client = {
        ...payload,
        id: clients.length + 1,
        workplaces: newClient.workplaces.map((wp, index) => ({
          ...wp,
          id: index + 1
        })),
        projects: Array.from(new Set((newClient.workplaces || [])
          .map(wp => (wp.description || '').trim())
          .filter(Boolean))),
        totalBilled: 0,
        outstanding: 0
      };
      setClients(prev => [...prev, client]);
    }
    
    //  
    setNewClient({
      type: 'PERSON',
      name: '',
      phone: '',
      mobile: '',
      email: '',
      address: '',
      notes: '',
      business: {
        businessName: '',
        representative: '',
        businessNumber: '',
        businessType: '',
        businessItem: '',
        businessAddress: '',
        taxEmail: ''
      },
      workplaces: [{ name: '', address: '', description: '' }]
    });
    setShowModal(false);
    setIsEditing(false);
    setEditingClientId(null);
  };

  const viewClientDetails = (client) => {
    setSelectedClient(client);
  };

  const handleEditClient = (client) => {
    setIsEditing(true);
    setEditingClientId(client.id);
    setNewClient({
      type: client.type || 'PERSON',
      name: client.name,
      phone: client.phone,
      mobile: client.mobile || '',
      email: client.email,
      address: client.address,
      notes: client.notes,
      business: client.business || {
        businessName: '',
        representative: '',
        businessNumber: '',
        businessType: '',
        businessItem: '',
        businessAddress: '',
        taxEmail: ''
      },
      workplaces: client.workplaces || [{ name: '', address: '', description: '' }]
    });
    setShowModal(true);
  };

  // : / (invoices , clientId / )
  const totalsByClientId = useMemo(() => {
    const map = new Map();
    clients.forEach(c => map.set(c.id, { total: 0, outstanding: 0 }));
    (invoices || []).forEach(inv => {
      const amount = Number(inv.amount) || 0;
      let cid = inv.clientId != null && inv.clientId !== '' ? parseInt(inv.clientId) : null;
      if (!cid) {
        const match = clients.find(c => c.name === inv.client);
        cid = match ? match.id : null;
      }
      if (!cid) return;
      const agg = map.get(cid) || { total: 0, outstanding: 0 };
      agg.total += amount;
      if (inv.status !== '') agg.outstanding += amount;
      map.set(cid, agg);
    });
    return map;
  }, [clients, invoices]);

  const grandTotals = useMemo(() => {
    let total = 0;
    let outstanding = 0;
    (invoices || []).forEach(inv => {
      const amount = Number(inv.amount) || 0;
      total += amount;
      if (inv.status !== '') outstanding += amount;
    });
    return { total, outstanding };
  }, [invoices]);

  //  : clients.projects + workItems.projectName + invoices.project  
  const projectCountsByClientId = useMemo(() => {
    const sets = new Map();
    const ensureSet = (id) => {
      if (!sets.has(id)) sets.set(id, new Set());
      return sets.get(id);
    };

    //  client.projects 
    clients.forEach(c => {
      const s = ensureSet(c.id);
      (c.projects || []).forEach(p => {
        const v = (p || '').trim();
        if (v) s.add(v);
      });
    });

    // workItems.projectName 
    (workItems || []).forEach(wi => {
      if (!wi) return;
      const cid = wi.clientId;
      if (!cid) return;
      const v = (wi.projectName || '').trim();
      if (!v) return;
      ensureSet(cid).add(v);
    });

    // invoices.project  (clientId ,   )
    (invoices || []).forEach(inv => {
      const v = (inv.project || '').trim();
      if (!v) return;
      let cid = inv.clientId != null && inv.clientId !== '' ? parseInt(inv.clientId) : null;
      if (!cid) {
        const match = clients.find(c => c.name === inv.client);
        cid = match ? match.id : null;
      }
      if (!cid) return;
      ensureSet(cid).add(v);
    });

    //   
    const counts = new Map();
    for (const [id, set] of sets.entries()) counts.set(id, set.size);
    return counts;
  }, [clients, workItems, invoices]);

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2"> </h1>
          <p className="text-gray-600">     </p>
        </div>
        <div className="flex space-x-2">
          {selectedIds.length > 0 && (
            <button
              onClick={() => setShowConfirmDelete(true)}
              className="bg-red-600 hover:bg-red-700 text-white font-bold py-2 px-4 rounded flex items-center"
              title="   "
            >
                ({selectedIds.length})
            </button>
          )}
          <button
            onClick={handleDownloadTemplate}
            className="bg-gray-600 hover:bg-gray-700 text-white font-bold py-2 px-4 rounded flex items-center"
          >
              
          </button>
          <button
            onClick={() => fileInputRef.current?.click()}
            className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded flex items-center"
          >
             Excel 
          </button>
          <button
            onClick={handleExportToExcel}
            className="bg-orange-600 hover:bg-orange-700 text-white font-bold py-2 px-4 rounded flex items-center"
          >
             Excel 
          </button>
          <button
            onClick={() => setShowModal(true)}
            className="bg-green-600 hover:bg-green-700 text-white font-bold py-2 px-4 rounded"
          >
            +  
          </button>
        </div>
      </div>

      {/*    */}
      <input
        type="file"
        ref={fileInputRef}
        onChange={handleImportFromExcel}
        accept=".xlsx,.xls"
        style={{ display: 'none' }}
      />

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 mb-1"> </p>
              <p className="text-xl font-bold text-gray-900">{clients.length}</p>
            </div>
            <div className="bg-blue-500 rounded-full p-3 text-white text-2xl">
              
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 mb-1">  :</p>
              <p className="text-xl font-bold text-green-600">
                {grandTotals.total.toLocaleString()}
              </p>
            </div>
            <div className="bg-green-500 rounded-full p-3 text-white text-2xl">
              
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 mb-1"></p>
              <p className="text-xl font-bold text-red-600">
                {grandTotals.outstanding.toLocaleString()}
              </p>
            </div>
            <div className="bg-red-500 rounded-full p-3 text-white text-2xl">
              
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 mb-1"> </p>
              <p className="text-xl font-bold text-orange-600">
                {clients.filter(c => (totalsByClientId.get(c.id)?.outstanding || 0) > 0).length}
              </p>
            </div>
            <div className="bg-orange-500 rounded-full p-3 text-white text-2xl">
              
            </div>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                <input
                  type="checkbox"
                  className="rounded border-gray-300"
                  checked={allSelected}
                  onChange={(e) => toggleSelectAll(e.target.checked)}
                  title=" "
                />
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                 
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                 
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                
              </th>
              <th className="px-6 py-3 text-left text-base font-medium text-gray-500 uppercase tracking-wider">
                
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {clients.map((client) => (
              <tr key={client.id} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">
                  <input
                    type="checkbox"
                    className="rounded border-gray-300"
                    checked={selectedIds.includes(client.id)}
                    onChange={(e) => toggleSelectOne(client.id, e.target.checked)}
                    title=" "
                  />
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="flex items-center">
                    <div className="h-10 w-10 bg-gray-300 rounded-full flex items-center justify-center">
                      <span className="text-sm font-medium text-gray-700">
                        {client.name.charAt(0)}
                      </span>
                    </div>
                    <div className="ml-4">
                      <div className="text-sm font-medium text-gray-900">
                        {client.name}
                        {client.type === 'BUSINESS' && (
                          <span className="ml-2 text-xs px-2 py-0.5 rounded bg-yellow-100 text-yellow-800"></span>
                        )}
                      </div>
                      <div className="text-sm text-gray-500">
                        {client.email}
                        {client.type === 'BUSINESS' && client.business?.businessNumber && (
                          <span className="ml-2">: {client.business.businessNumber}</span>
                        )}
                      </div>
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">
                    {client.phone && <div>: {client.phone}</div>}
                    {client.mobile && <div>: {client.mobile}</div>}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">{client.address}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">{(projectCountsByClientId.get(client.id) || 0)}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900">
                    {(totalsByClientId.get(client.id)?.total || 0).toLocaleString()}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className={`text-sm font-medium ${
                    (totalsByClientId.get(client.id)?.outstanding || 0) > 0 ? 'text-red-600' : 'text-green-600'
                  }`}>
                    {(totalsByClientId.get(client.id)?.outstanding || 0).toLocaleString()}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                  <button 
                    onClick={() => viewClientDetails(client)}
                    className="text-blue-600 hover:text-blue-900 mr-2"
                  >
                    
                  </button>
                  <button 
                    className="text-green-600 hover:text-green-900 mr-2"
                    onClick={() => handleEditClient(client)}
                  >
                    
                  </button>
                  <button className="text-red-600 hover:text-red-900"></button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/*     */}
      {showConfirmDelete && (
        <div className="fixed inset-0 bg-black bg-opacity-30 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-lg w-full max-w-md p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-2"> </h3>
            <p className="text-sm text-gray-600 mb-4"> {selectedIds.length}  ?     .</p>
            <div className="flex justify-end gap-2">
              <button className="btn-secondary" onClick={() => setShowConfirmDelete(false)}></button>
              <button className="btn-primary bg-red-600 hover:bg-red-700" onClick={handleBulkDelete}></button>
            </div>
          </div>
        </div>
      )}

      {/*    */}
      {showModal && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-10 mx-auto p-5 border w-4/5 max-w-2xl shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                {isEditing ? '  ' : '  '}
              </h3>
              <form onSubmit={handleSubmit} className="space-y-4">
                {/*   */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                  <div className="flex items-center gap-6">
                    <label className="inline-flex items-center gap-2 text-sm">
                      <input
                        type="radio"
                        name="type"
                        value="PERSON"
                        checked={newClient.type === 'PERSON'}
                        onChange={() => setNewClient(prev => ({ ...prev, type: 'PERSON' }))}
                      />
                      
                    </label>
                    <label className="inline-flex items-center gap-2 text-sm">
                      <input
                        type="radio"
                        name="type"
                        value="BUSINESS"
                        checked={newClient.type === 'BUSINESS'}
                        onChange={() => setNewClient(prev => ({ ...prev, type: 'BUSINESS' }))}
                      />
                      
                    </label>
                  </div>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <input
                    type="text"
                    name="name"
                    value={newClient.name}
                    onChange={handleInputChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                    required
                  />
                </div>
                {/*   */}
                {newClient.type === 'BUSINESS' && (
                  <div className="border rounded-md p-3 bg-yellow-50">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                      <div>
                        <label className="block text-sm font-medium text-gray-700"></label>
                        <input
                          type="text"
                          name="businessName"
                          value={newClient.business.businessName}
                          onChange={handleBusinessChange}
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                          required={newClient.type === 'BUSINESS'}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700"></label>
                        <input
                          type="text"
                          name="representative"
                          value={newClient.business.representative}
                          onChange={handleBusinessChange}
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                          required={newClient.type === 'BUSINESS'}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700"></label>
                        <input
                          type="text"
                          name="businessNumber"
                          value={newClient.business.businessNumber}
                          onChange={handleBusinessChange}
                          placeholder="000-00-00000"
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                          required={newClient.type === 'BUSINESS'}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700"> </label>
                        <input
                          type="email"
                          name="taxEmail"
                          value={newClient.business.taxEmail}
                          onChange={handleBusinessChange}
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                          required={newClient.type === 'BUSINESS'}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700"></label>
                        <input
                          type="text"
                          name="businessType"
                          value={newClient.business.businessType}
                          onChange={handleBusinessChange}
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700"></label>
                        <input
                          type="text"
                          name="businessItem"
                          value={newClient.business.businessItem}
                          onChange={handleBusinessChange}
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                        />
                      </div>
                      <div className="md:col-span-2">
                        <label className="block text-sm font-medium text-gray-700"> </label>
                        <input
                          type="text"
                          name="businessAddress"
                          value={newClient.business.businessAddress}
                          onChange={handleBusinessChange}
                          className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                        />
                      </div>
                    </div>
                  </div>
                )}
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <input
                    type="tel"
                    name="phone"
                    value={newClient.phone}
                    onChange={handleInputChange}
                    placeholder="02-1234-5678"
                    className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <input
                    type="tel"
                    name="mobile"
                    value={newClient.mobile}
                    onChange={handleInputChange}
                    placeholder="010-1234-5678"
                    className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <input
                    type="email"
                    name="email"
                    value={newClient.email}
                    onChange={handleInputChange}
                    placeholder=""
                    className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <textarea
                    name="address"
                    value={newClient.address}
                    onChange={handleInputChange}
                    rows="2"
                    placeholder=""
                    className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <textarea
                    name="notes"
                    value={newClient.notes}
                    onChange={handleInputChange}
                    rows="3"
                    className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2"
                  />
                </div>
                
                {/*   */}
                <div>
                  <div className="flex justify-between items-center mb-2">
                    <label className="block text-sm font-medium text-gray-700"> </label>
                    <button
                      type="button"
                      onClick={addWorkplace}
                      className="text-blue-600 hover:text-blue-800 text-sm"
                    >
                      +  
                    </button>
                  </div>
                  
                  {newClient.workplaces.map((workplace, index) => (
                    <div key={index} className="border rounded-md p-3 mb-3 bg-gray-50">
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm font-medium text-gray-700"> {index + 1}</span>
                        {newClient.workplaces.length > 1 && (
                          <button
                            type="button"
                            onClick={() => removeWorkplace(index)}
                            className="text-red-600 hover:text-red-800 text-sm"
                          >
                            
                          </button>
                        )}
                      </div>
                      
                      <div className="space-y-2">
                        <input
                          type="text"
                          placeholder=" (:  ,   )"
                          value={workplace.name}
                          onChange={(e) => handleWorkplaceChange(index, 'name', e.target.value)}
                          className="w-full border border-gray-300 rounded px-2 py-1 text-sm"
                          required
                        />
                        <input
                          type="text"
                          placeholder=" "
                          value={workplace.address}
                          onChange={(e) => handleWorkplaceChange(index, 'address', e.target.value)}
                          className="w-full border border-gray-300 rounded px-2 py-1 text-sm"
                          required
                        />
                        <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                        <textarea
                          placeholder=" ()"
                          value={workplace.description}
                          onChange={(e) => handleWorkplaceChange(index, 'description', e.target.value)}
                          className="w-full border border-gray-300 rounded px-2 py-1 text-sm"
                          rows="2"
                          required
                        />
                        <p className="mt-1 text-xs text-red-600">:  .</p>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="flex justify-end space-x-2 pt-4">
                  <button
                    type="button"
                    onClick={() => {
                      setShowModal(false);
                      setIsEditing(false);
                      setEditingClientId(null);
                      setNewClient({
                        name: '',
                        phone: '',
                        mobile: '',
                        email: '',
                        address: '',
                        notes: '',
                        workplaces: [{ name: '', address: '', description: '' }]
                      });
                    }}
                    className="px-4 py-2 text-gray-500 hover:text-gray-700"
                  >
                    
                  </button>
                  <button
                    type="submit"
                    className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded"
                  >
                    {isEditing ? '' : ''}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {/*    */}
      {selectedClient && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-10 mx-auto p-5 border w-4/5 max-w-3xl shadow-lg rounded-md bg-white">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-medium text-gray-900">   - {selectedClient.name}</h3>
              <button
                onClick={() => setSelectedClient(null)}
                className="text-gray-400 hover:text-gray-600"
              >
                
              </button>
            </div>
            
            <div className="border rounded-lg p-6 bg-gray-50">
              <div className="grid grid-cols-2 gap-6 mb-6">
                <div>
                  <h4 className="font-medium mb-3"> </h4>
                  <p className="mb-2"><strong>:</strong> {selectedClient.name}</p>
                  {selectedClient.phone && <p className="mb-2"><strong>:</strong> {selectedClient.phone}</p>}
                  {selectedClient.mobile && <p className="mb-2"><strong>:</strong> {selectedClient.mobile}</p>}
                  {selectedClient.email && <p className="mb-2"><strong>:</strong> {selectedClient.email}</p>}
                  <p><strong>:</strong> {selectedClient.address}</p>
                </div>
                <div>
                  <h4 className="font-medium mb-3"> </h4>
                  <p className="mb-2"><strong> :</strong> {selectedClient.totalBilled.toLocaleString()}</p>
                  <p className="mb-2"><strong>:</strong> 
                    <span className={selectedClient.outstanding > 0 ? 'text-red-600 font-medium' : 'text-green-600'}>
                      {selectedClient.outstanding.toLocaleString()}
                    </span>
                  </p>
                  <p><strong> :</strong> {selectedClient.projects.length}</p>
                </div>
              </div>

              {selectedClient.type === 'BUSINESS' && (
                <div className="border rounded-md p-4 bg-yellow-50 mb-6">
                  <h4 className="font-medium mb-3"> </h4>
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <p><strong>:</strong> {selectedClient.business?.businessName || '-'}</p>
                    <p><strong>:</strong> {selectedClient.business?.representative || '-'}</p>
                    <p><strong>:</strong> {selectedClient.business?.businessNumber || '-'}</p>
                    <p><strong> :</strong> {selectedClient.business?.taxEmail || '-'}</p>
                    <p><strong>:</strong> {selectedClient.business?.businessType || '-'}</p>
                    <p><strong>:</strong> {selectedClient.business?.businessItem || '-'}</p>
                    <p className="col-span-2"><strong> :</strong> {selectedClient.business?.businessAddress || '-'}</p>
                  </div>
                </div>
              )}
              
              <div className="mb-4">
                <h4 className="font-medium mb-2"> </h4>
                {selectedClient.workplaces && selectedClient.workplaces.length > 0 ? (
                  <div className="space-y-3">
                    {selectedClient.workplaces.map((workplace, index) => (
                      <div key={index} className="bg-white rounded border p-3">
                        <div className="flex justify-between items-start mb-2">
                          <h5 className="font-medium text-gray-900">{workplace.name}</h5>
                          <span className="text-xs bg-blue-100 text-blue-600 px-2 py-1 rounded">
                             {index + 1}
                          </span>
                        </div>
                        <p className="text-sm text-gray-600 mb-1">
                          <strong>:</strong> {workplace.address}
                        </p>
                        {workplace.description && (
                          <p className="text-sm text-gray-600">
                            <strong>:</strong> {workplace.description}
                          </p>
                        )}
                      </div>
                    ))}
                  </div>
                ) : (
                  <p className="text-gray-500 italic">  .</p>
                )}
              </div>

              <div className="mb-4">
                <h4 className="font-medium mb-2"> </h4>
                {selectedClient.projects.length > 0 ? (
                  <ul className="bg-white rounded border p-3">
                    {selectedClient.projects.map((project, index) => (
                      <li key={index} className="py-1 border-b last:border-b-0">
                         {project}
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="text-gray-500 italic">  .</p>
                )}
              </div>
              
              {selectedClient.notes && (
                <div>
                  <h4 className="font-medium mb-2"></h4>
                  <p className="bg-white rounded border p-3">{selectedClient.notes}</p>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Clients;
