import * as XLSX from 'xlsx';

//    
export const exportToExcel = {
  //   
  clients: (clients) => {
    const data = clients.map(client => ({
      'ID': client.id,
      '': client.name,
      '': client.phone,
      '': client.email,
      '': client.address,
      '': client.projects.join(', '),
      ' ': client.totalBilled,
      '': client.outstanding,
      '': client.notes
    }));

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, ' ');
    
    //   
    worksheet['!cols'] = [
      { width: 8 },   // ID
      { width: 15 },  // 
      { width: 15 },  // 
      { width: 25 },  // 
      { width: 30 },  // 
      { width: 20 },  // 
      { width: 15 },  //  
      { width: 15 },  // 
      { width: 30 }   // 
    ];
    
    XLSX.writeFile(workbook, '_.xlsx');
  },

  //    
  workItems: (workItems) => {
    const data = workItems.map(item => ({
      'ID': item.id,
      '': item.clientName,
      '': item.workplaceName,
      '': item.projectName,
      '': item.name,
      '': item.category,
      '': item.defaultPrice,
      '': item.quantity || 1,
      '': item.unit,
      '': (item.defaultPrice || 0) * (item.quantity || 1),
      '': item.laborPersons || '',
      '': item.laborUnitRate || '',
      '': ((parseInt(item.laborPersons || 0, 10) || 0) * (parseInt(item.laborUnitRate || 0, 10) || 0)),
      '()': ((item.defaultPrice || 0) * (item.quantity || 1)) + ((parseInt(item.laborPersons || 0, 10) || 0) * (parseInt(item.laborUnitRate || 0, 10) || 0)),
      '': item.description,
      '': item.status,
      '': item.date,
      '': item.notes
    }));

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, ' ');
    
    worksheet['!cols'] = [
      { width: 8 },   // ID
      { width: 12 },  // 
      { width: 20 },  // 
      { width: 15 },  // 
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 15 },  // 
      { width: 8 },   // 
      { width: 8 },   // 
      { width: 15 },  // 
      { width: 10 },  // 
      { width: 15 },  // 
      { width: 15 },  // 
      { width: 18 },  // ()
      { width: 30 },  // 
      { width: 10 },  // 
      { width: 12 },  // 
      { width: 25 }   // 
    ];
    
    XLSX.writeFile(workbook, '_.xlsx');
  },

  //   
  invoices: (invoices) => {
    const data = invoices.map(invoice => ({
      '': invoice.id,
      '': invoice.client,
      '': invoice.project,
      '': invoice.workplaceAddress,
      '': invoice.amount,
      '': invoice.status,
      '': invoice.date,
      '': invoice.dueDate,
      '': invoice.workItems.length
    }));

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, ' ');
    
    worksheet['!cols'] = [
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 15 },  // 
      { width: 35 },  // 
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 12 },  // 
      { width: 12 },  // 
      { width: 12 }   // 
    ];
    
    XLSX.writeFile(workbook, '_.xlsx');
  },

  //   
  estimates: (estimates) => {
    const data = estimates.map(estimate => ({
      '': estimate.id,
      '': estimate.clientName,
      '': estimate.projectName,
      '': estimate.workplaceAddress,
      '': estimate.totalAmount,
      '': estimate.status,
      '': estimate.date,
      '': estimate.validUntil,
      '': estimate.items ? estimate.items.length : 0
    }));

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, ' ');
    
    worksheet['!cols'] = [
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 15 },  // 
      { width: 35 },  // 
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 12 },  // 
      { width: 12 },  // 
      { width: 12 }   // 
    ];
    
    XLSX.writeFile(workbook, '_.xlsx');
  },

  //   
  estimateDetail: (estimate) => {
    const headerData = [
      [' ', estimate.id],
      ['', estimate.clientName],
      ['', estimate.projectName],
      [' ', estimate.workplaceAddress],
      ['', estimate.date],
      [' ', estimate.validUntil],
      ['', estimate.status],
      [' ', estimate.totalAmount],
      []
    ];

    const workItemData = estimate.items.map((item, index) => ({
      '': index + 1,
      '': item.name,
      '': item.quantity,
      '': item.unitPrice,
      '': item.total,
      '': item.category,
      '': item.description,
      '': item.notes
    }));

    const workbook = XLSX.utils.book_new();
    
    //  
    const headerSheet = XLSX.utils.aoa_to_sheet(headerData);
    XLSX.utils.book_append_sheet(workbook, headerSheet, ' ');
    
    //   
    const workItemSheet = XLSX.utils.json_to_sheet(workItemData);
    XLSX.utils.book_append_sheet(workbook, workItemSheet, ' ');
    
    workItemSheet['!cols'] = [
      { width: 8 },   // 
      { width: 20 },  // 
      { width: 10 },  // 
      { width: 15 },  // 
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 30 },  // 
      { width: 25 }   // 
    ];
    
    XLSX.writeFile(workbook, `_${estimate.id}.xlsx`);
  },

  //   
  invoiceDetail: (invoice) => {
    const headerData = [
      [' ', invoice.id],
      ['', invoice.client],
      ['', invoice.project],
      [' ', invoice.workplaceAddress],
      ['', invoice.date],
      [' ', invoice.dueDate],
      ['', invoice.status],
      [' ', invoice.amount],
      []
    ];

    const workItemData = invoice.workItems.map((item, index) => ({
      '': index + 1,
      '': item.name,
      '': item.quantity,
      '': item.unitPrice,
      '': item.total,
      '': item.category,
      '': item.description,
      '': item.notes
    }));

    const workbook = XLSX.utils.book_new();
    
    //  
    const headerSheet = XLSX.utils.aoa_to_sheet(headerData);
    XLSX.utils.book_append_sheet(workbook, headerSheet, ' ');
    
    //   
    const workItemSheet = XLSX.utils.json_to_sheet(workItemData);
    XLSX.utils.book_append_sheet(workbook, workItemSheet, ' ');
    
    workItemSheet['!cols'] = [
      { width: 8 },   // 
      { width: 20 },  // 
      { width: 10 },  // 
      { width: 15 },  // 
      { width: 15 },  // 
      { width: 12 },  // 
      { width: 30 },  // 
      { width: 25 }   // 
    ];
    
    XLSX.writeFile(workbook, `_${invoice.id}.xlsx`);
  }
};

//    
export const importFromExcel = {
  //   
  clients: (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const data = new Uint8Array(e.target.result);
          const workbook = XLSX.read(data, { type: 'array' });
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          const jsonData = XLSX.utils.sheet_to_json(worksheet);
          
          const clients = jsonData.map((row, index) => ({
            id: row['ID'] || index + 1,
            name: row[''] || '',
            phone: row[''] || '',
            email: row[''] || '',
            address: row[''] || '',
            projects: row[''] ? row[''].split(', ') : [],
            totalBilled: row[' '] || 0,
            outstanding: row[''] || 0,
            notes: row[''] || '',
            workplaces: [] // 
          }));
          
          resolve(clients);
        } catch (error) {
          reject(error);
        }
      };
      reader.readAsArrayBuffer(file);
    });
  },

  //    
  workItems: (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const data = new Uint8Array(e.target.result);
          const workbook = XLSX.read(data, { type: 'array' });
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          const jsonData = XLSX.utils.sheet_to_json(worksheet);
          
          const workItems = jsonData.map((row, index) => ({
            id: row['ID'] || index + 1,
            clientId: row['ID'] || 1,
            clientName: row[''] || '',
            workplaceId: row['ID'] || 1,
            workplaceName: row[''] || '',
            projectName: row[''] || '',
            name: row[''] || '',
            category: row[''] || '',
            defaultPrice: row[''] || 0,
            quantity: row[''] || 1,
            unit: row[''] || '',
            description: row[''] || '',
            status: row[''] || '',
            date: row[''] || new Date().toISOString().split('T')[0],
            notes: row[''] || '',
            //  : /   
            laborPersons: row[''] ?? row['LaborPersons'] ?? '',
            laborUnitRate: row[''] ?? row['LaborUnitRate'] ?? ''
          }));
          
          resolve(workItems);
        } catch (error) {
          reject(error);
        }
      };
      reader.readAsArrayBuffer(file);
    });
  },

  //   
  estimates: (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const data = new Uint8Array(e.target.result);
          const workbook = XLSX.read(data, { type: 'array' });
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          const jsonData = XLSX.utils.sheet_to_json(worksheet);
          
          const estimates = jsonData.map((row, index) => ({
            id: row[''] || `EST-${Date.now()}-${index + 1}`,
            clientName: row[''] || '',
            projectName: row[''] || '',
            workplaceAddress: row[''] || '',
            totalAmount: row[''] || 0,
            status: row[''] || '',
            date: row[''] || new Date().toISOString().split('T')[0],
            validUntil: row[''] || '',
            items: [], // 
            notes: row[''] || ''
          }));
          
          resolve(estimates);
        } catch (error) {
          reject(error);
        }
      };
      reader.readAsArrayBuffer(file);
    });
  }
};

